.login-container {
    max-width: 40%;
    margin: auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
    box-sizing: border-box;
}

.login-title {
    text-align: center;
    color: #333;
    margin-bottom: 20px;
}

.login-input, .login-password {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    border: 1px solid #ddd;
    box-sizing: border-box;
}

.login-button {
    width: 100%;
    padding: 10px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.login-button:hover {
    background-color: #45a049;
}

.login-link {
    text-align: center;
    color: #007BFF;
    margin-top: 10px;
}

@media (max-width: 768px) {
    .login-container {
        max-width: 80%;
        padding: 15px;
    }

    .login-input, .login-password {
        padding: 8px;
    }
}

@media (max-width: 480px) {
    .login-container {
        max-width: 95%;
        padding: 10px;
    }
}


.register-button {
    display: inline-block;
    padding: 10px;
    background-color: #007BFF;
    color: white;
    border-radius: 5px;
    text-align: center;
    width: 100%;
    transition: background-color 0.3s ease;
}

.register-button:hover {
    background-color: #0056b3;
}
